<template>
<div class="page-warp">
  <skeleton :nums="4" :iheight="'.4rem'" v-if="isSkeletonLoading"/>
  <div class="container" v-else>
    <div class="header-container" ref="nvaHeader">
      <van-nav-bar v-if="!(isMiniProgram || isByteDanceMP || isAlipayMp)" :title="title" fixed>
        <div slot="left" @click="goBack" class="back-container" v-if="!$route.query.browerFirstIn">
          <img class="icon-back" :src="$globalIcon.blackBackIcon" />
        </div>
        <div v-if="isFromRN || isUplus || isFromIhaier" slot="right" @click="toShare" class="back-container">
          <img class="icon-share" :src="$globalIcon.blackShareIcon" />
        </div>
      </van-nav-bar>
    </div>
    <div class="content-container" @scroll="handleScroll" :style="{ paddingTop: !(isMiniProgram || isByteDanceMP || isAlipayMp) ? (0.92 + 'rem') : 0, }" ref="contentContainer">
        <div v-for="(item, index) in modualList" :key="`${index}-${item.id}`" :id="item.id">
          <!-- 轮播 -->
          <div :id="`anchor${item.id}`" :ref="`anchor${item.id}`" :style="{ backgroundColor: item.backgroundColor }" v-if="item.modualType === 'SWITCH'">
            <van-swipe :autoplay="3000" indicator-color="black">
              <van-swipe-item v-for="(banner, banIn) in item.imageList" :key="banIn">
                <van-image lazy-load class="banner" fit="cover" width="100%" height="auto" :src="banner.imageUrl.replace('http:', 'https:') | imageToWebp($webpSurpport)" @click="bannerClick(banner)" />
              </van-swipe-item>
            </van-swipe>
          </div>
          <!-- NAVIGATIONBAR 切换 -->
          <div
            :id="`anchor${item.id}`"
            :ref="`anchor${item.id}`"
            style="{backgroundColor: 'white'}"
            v-if="item.modualType == 'NAVIGATIONBAR' && item.col == 1 && item.navigationList && item.itemType == 'NAVIGATIONBAR'"
          >
            <van-tabs color="black" v-model="activeNavId" @click="changeNavigationbar">
              <van-tab v-for="(tab, inx) in item.navigationList" :title="tab.modualTitle" :name="tab.modualId" title-style="tab-title" :key="inx"></van-tab>
            </van-tabs>
          </div>
          <!-- 优惠卷位置 -->
          <div
            v-if="item.bannerImage && item.modualType == 'TABLE' && item.itemType == 'COUPON' && item.couponList"
            class="customxvv_header_image"
            :style="{ backgroundColor: item.backgroundColor }"
            :id="item.id"
          >
            <img :v-lazy="item.bannerImage.replace('http:', 'https:') | imageToWebp($webpSurpport)" />
          </div>
          <div
            class="showyhj"
            v-if="item.modualType == 'TABLE' && item.itemType == 'COUPON' && item.couponList"
            :style="{ backgroundColor: item.backgroundColor }"
            :id="item.id"
          >
            <div v-for="(itemChild, indexChild) in item.couponList" class="coupon-block" :key="indexChild">
              <img src="@/assets/imgs/banner/im-quan.png" alt width="100%" height="100%" />
              <div class="text1">
                <span>¥</span>
                <span>{{ couponMap[itemChild.couponId].couponValue }}</span>
              </div>
              <div class="text2">{{ couponMap[itemChild.couponId].couponName }}</div>
              <div class="text3" @click="getCoupon(itemChild.activityCode,itemChild.couponTemplateCode,itemChild.sourceChannel)">
                <span>立即领取</span>
              </div>
              <img
                v-if="couponMap[itemChild.couponId].getNum >= couponMap[itemChild.couponId].personLimitNum"
                src="@/assets/imgs/banner/im-yl.png"
                class="showyhjimg"
              />
              <img
                v-if="couponMap[itemChild.couponId].totalGetNum >= couponMap[itemChild.couponId].totalLimitNum"
                src="@/assets/imgs/banner/juanyqw.png"
                class="showyhjimg"
              />
            </div>
          </div>
          <!-- 优惠卷位置 -->
          <!-- 活动图片 1行、2行、2列和3列 -->
          <div
            :id="`anchor${item.id}`"
            :ref="`anchor${item.id}`"
            :style="{ backgroundColor: item.backgroundColor }"
            v-if="item.modualType === 'TABLE' && item.tableImageList && item.itemType === 'IMAGE'"
          >
            <div v-if="item.bannerImage" class="custom-banner-image">
              <img :v-lazy="item.bannerImage.replace('http:', 'https:') | imageToWebp($webpSurpport)" />
            </div>
            <van-grid :border="false" :column-num="item.col === 3 ? 3 : 2">
              <van-grid-item v-for="(prod, key) in item.tableImageList" @click="bannerClick(prod)" :key="key">
                <van-image lazy-load :src="prod.imageUrl.replace('http:', 'https:') | imageToWebp($webpSurpport)" />
              </van-grid-item>
            </van-grid>
            <!-- <div class="img-container">
            <div
              v-for="(prod, key) in item.tableImageList" :key="key"
              @click="bannerClick(prod)"
              :style="{backgroundColor: item.backgroundColor,width: (100/item.col) + '%', display: 'inline-block'}">
              <img :src="prod.imageUrl.replace('http:','https:')" class="img-item" />
            </div>
            </div>-->
          </div>
          <!-- 两列商品 -->
          <div
            :id="`anchor${item.id}`"
            :ref="`anchor${item.id}`"
            :style="{ backgroundColor: item.backgroundColor }"
            v-if="item.modualType == 'TABLE' && item.col == 2 && !item.tabList && item.itemType == 'SKU'"
          >
            <div v-if="item.bannerImage" class="custom-banner-image">
              <img :v-lazy="item.bannerImage.replace('http:', 'https:') | imageToWebp($webpSurpport)" />
            </div>
            <div class="img-container">
              <div v-for="(prod, pIndex) in item.skuList" :key="pIndex" style="display:inline-block;position:relative;">
                <div v-if="skuMap[prod.sku] !== undefined" @click="toProdDetail(skuMap[prod.sku], prod.sku)" :id="prod.sku" class="col-two">
                  <van-image
                    lazy-load
                    :src="skuMap[prod.sku].defaultImageUrl.replace('http:', 'https:') | imageToWebp($webpSurpport)"
                    fit="contain"
                    height="3.6rem"
                    width="3.2rem"
                    class="prod-img" />
                  <div class="title">{{ skuMap[prod.sku].productFullName }}</div>
                  <div class="float-price">
                    <span class="int-price">￥{{ skuMap[prod.sku].finalPrice | splitInt }}</span>
                    {{ skuMap[prod.sku].finalPrice | splitFloat }}
                  </div>
                  <div class="saleTag" :style="{ 'border-color': prod.tagColor+' !important', 'background-color': prod.tagColor+' !important' }" v-if="prod.tagColor && prod.tagName">{{ prod.tagName }}</div>
                </div>
              </div>
            </div>
          </div>
          <!-- 三列商品 -->
          <div
            :id="`anchor${item.id}`"
            :ref="`anchor${item.id}`"
            :style="{ backgroundColor: item.backgroundColor }"
            v-if="item.modualType == 'TABLE' && item.col == 3 && !item.tabList && item.itemType == 'SKU'"
          >
            <div v-if="item.bannerImage" class="custom-banner-image">
              <img :v-lazy="item.bannerImage.replace('http:', 'https:') | imageToWebp($webpSurpport)" />
            </div>
            <div class="col-three-container">
              <div v-for="(prod, pIndex) in item.skuList" :key="pIndex" @click="toProdDetail(skuMap[prod.sku], prod.sku)" class="col-three">
                <van-image
                  lazy-load
                  :src="skuMap[prod.sku].defaultImageUrl.replace('http:', 'https:') | imageToWebp($webpSurpport)"
                  fit="contain"
                  width="93%"
                  height="2.1rem"
                  class="col-three-img" />
                <div class="title">{{ skuMap[prod.sku].productFullName }}</div>
                <div class="float-price">
                  <span class="int-price">￥{{ skuMap[prod.sku].finalPrice | splitInt }}</span>
                  {{ skuMap[prod.sku].finalPrice | splitFloat }}
                </div>
                <div class="saleTag" :style="{ 'border-color': prod.tagColor+' !important', 'background-color': prod.tagColor+' !important' }" v-if="prod.tagColor && prod.tagName">{{ prod.tagName }}</div>
              </div>
            </div>
          </div>
        </div>
    </div>
    <!-- navigation悬停 -->
    <div v-if="showNavBar">
      <div :style="{ position: 'absolute', top: headerHeight + 'px', width: 100 + 'vw' }" v-for="(item, index) in modualList" :key="'nav' + index">
        <!-- NAVIGATIONBAR 切换 -->
        <div
          :id="`anchor${item.id}`"
          style="{backgroundColor: 'white'}"
          v-if="item.modualType == 'NAVIGATIONBAR' && item.col == 1 && item.navigationList && item.itemType == 'NAVIGATIONBAR'"
        >
          <van-tabs color="black" v-model="activeNavId" @click="changeNavigationbar">
            <van-tab v-for="(tab, inx) in item.navigationList" :title="tab.modualTitle" :name="tab.modualId" title-style="tab-title" :key="inx"></van-tab>
          </van-tabs>
        </div>
      </div>
    </div>
    <!-- 返回顶部 -->
    <top-feedBack :isShow="showToTopFlag" @hiddenFeedBack="hiddenFeedBack"  @toTop="fnScrollToTop"></top-feedBack>
  </div>
</div>
</template>
<script>
import utils from '@/utils/utils'
import UplusApi from '@uplus/uplus-api'
import bannerService from '@/http/service/banner'
// import { getUcLoginUrl } from '@/http/service/common.js';
import Skeleton from '@/components/skeleton/index.vue';
// import mixins from '@/components/top-feedBack/mixins_inpage_scroll.js'
import { mapGetters } from 'vuex'
// import config from '../../config'
const topFeedBack = () => import('@/components/top-feedBack/index')
const instance = new UplusApi()
instance.initDeviceReady()

const DEFAULTIMG = 'https://cdn09.ehaier.com/shunguang/H5/www/img/share_default.png'; // 分享默认图
const localData = {
  sharedesp: '', // 分享描述
  navBarHeight: 0, // tab切换组件高度
  navBarOffsetTop: 0, // tab切换组件距离头部距离
  shareimg: DEFAULTIMG, // 分享图片路径
  scrollTop: 0, // 记录滚动位置
};
export default {
  name: 'bannerCustom',
  // mixins: [mixins],
  components: {
    Skeleton,
    topFeedBack
  },
  data () {
    return {
      isSkeletonLoading: true,
      title: '',
      modualList: [],
      skuMap: {},
      couponMap: {}, // 优惠券列表
      showNavBar: false, // 是否显示navbar
      headerHeight: 0, // 标题栏高度
      activeNavId: 0, // 选中的tab组件id
      // location: {
      //   region: '山东 青岛 崂山区 中韩街道',
      //   provinceId: 16,
      //   province: '山东',
      //   cityId: 173,
      //   city: '青岛',
      //   districtId: 2450,
      //   district: '崂山区',
      //   townshipId: 12036596,
      //   township: '中韩街道',
      // }
      isAlipayMp: this.$util.isAlipay(),
      isMiniProgram: false,
      isByteDanceMP: false,
      isAliENV: false,
      isAliMP: false,
      isUplus: this.$util.isHaierApp(),
      isFromRN: this.$util.isSgRn(),
      isFromIhaier: this.$util.isIhaierApp(),
      showToTopFlag: false,
    }
  },
  computed: {
    ...mapGetters(['mId']),
    ...mapGetters({
      location: 'getlocationInfo',
      accessToken: 'accessToken',
    }),
  },
  created () {
  },
  // // 关闭直播
  // beforeRouteLeave(to, from, next) {
  //   if (this.$util.isUplus()) {
  //     let isShowLive = from.query.show_live
  //     console.log('itsolute beforeRouteLeave', to.name, isShowLive);
  //     if (+isShowLive && to.name !== 'detail') {
  //       instance.initDeviceReady().then(() => {
  //         instance.upLiveModule.closeFloatWindow().then(result => {
  //           if (result.retCode === '000000') {
  //             next()
  //           } else {
  //             next()
  //           }
  //         }).catch(() => {
  //           next()
  //         })
  //       }).catch(() => {
  //         next()
  //       })
  //     } else {
  //       next()
  //     }
  //   } else {
  //     next()
  //   }
  // },
  mounted () {
    this.$util.gioTrackWithVariable('mall_activity_view', {
      activity_id: this.$route.query.id,
      activity_type: '动态活动'
    })
    if (!utils.isHaierApp()) {
      if (utils.isWeixin()) {
        utils.loadScript('//res.wx.qq.com/open/js/jweixin-1.3.2.js').then(() => {
          wx.miniProgram.getEnv(res => {
            console.log(`是否为小程序环境：${res.miniprogram}`)
            // true
            if (res.miniprogram) {
              this.isMiniProgram = true
            } else {
              this.isMiniProgram = false
            }
          })
        })
      }

      utils.loadScript('//s3.pstatp.com/toutiao/tmajssdk/jssdk-1.0.1.js').then(() => {
        const isTTWebView = navigator.userAgent.toLowerCase().includes('toutiaomicroapp');
        this.isByteDanceMP = isTTWebView;
      })

      this.isAliENV = navigator.userAgent.includes('AliApp')
      if (this.isAliENV) {
        utils.loadScript('https://appx/web-view.min.js').then(() => {
          my.getEnv(res => {
            console.log(`是否为小程序环境：${res.miniprogram}`)
            if (res.miniprogram) {
              this.isAliMP = true;
            }
          })
        })
      }
    // } else {
    //   this.doShowUplusLive()
    } else {
      this.doShowUplusLive()
    }
    this.getDetail()
    setTimeout(() => {
      this.headerHeight = document.querySelector('.van-nav-bar').clientHeight
    }, 500);

    this.debouncedAction = this.$util.throttle(this.option, 100)
  },
  activated() {
    this.$nextTick(() => {
      this.$refs.contentContainer && this.$refs.contentContainer.scrollTo(0, localData.scrollTop || 0);
    });
  },
  methods: {
    /**
     * 隐藏 FeedBack
    */
    hiddenFeedBack() {
      this.showToTopFlag = false
    },
    option() {
      if (!this.$refs.contentContainer) return
      let _scrollTop = this.$refs.contentContainer.scrollTop
      this.showToTopFlag = _scrollTop > 200
    },
    /**
     * 返回顶部
    */
    fnScrollToTop() {
      // this.$refs.toTop.scrollIntoView({
      //   block: 'start',
      //   behavior: 'smooth', // 平滑过渡
      // }) // 回到顶部
      this.scrollSmoothTo(0)
    },
    /**
     * 模拟动画滚动
    */
    scrollSmoothTo(position) {
      if (!window.requestAnimationFrame) {
        window.requestAnimationFrame = function(callback) {
          return setTimeout(callback, 17);
        };
      }
      // debugger
      let _target = this.$refs.contentContainer
      // 当前滚动高度
      let scrollTop = _target.scrollTop;

      // 滚动step方法
      let step = function () {
        // 距离目标滚动距离
        var distance = position - scrollTop;
        // 目标滚动位置
        scrollTop = scrollTop + distance / 10;
        if (Math.abs(distance) < 1) {
          _target.scrollTo(0, position);
        } else {
          _target.scrollTo(0, scrollTop);
          requestAnimationFrame(step);
        }
      };
      step();
    },
    // 调用直播
    doShowUplusLive() {
      let isShowLive = this.$route.query.show_live
      if (+isShowLive && !sessionStorage.getItem('showlive')) {
        instance.initDeviceReady().then(() => {
          instance.upLiveModule.showFloatWindow()
        })
      }
    },
    // 领取优惠券
    getCoupon (activityCode, couponTemplateCode, sourceChannel) {
      // 登陆判断
      if (!this.$store.getters.getLoginStatus) {
        this.$store.dispatch('doLogin')
        return
      }
      const streetId = this.$store.getters['getlocationInfo'].townshipId;
      bannerService
        .getCoupon({
          activityCode,
          couponTemplateCode,
          sourceChannel,
          streetId,
        })
        .then(res => {
          this.$toast(res.data.message)
          if (res.data.success) {
            this.getDetail()
          }
        })
    },

    handleScroll (ev) {
      // 判断是否吸顶效果
      if (this.$refs.contentContainer.scrollTop >= localData.navBarOffsetTop) {
        this.showNavBar = true
      } else {
        this.showNavBar = false
      }
      this.debouncedAction()
    },
    /* 无用代码
    getCurrentLoaction () {
      console.log('开始定位')
      this.$store.dispatch('doLocation').then(res => {
        console.log('开始定位', res)
        if (res === 400) {
          console.log('定位异常')
        } else {
          console.log(res, 'bbb')
          this.$store.dispatch('doLocationInfo', res).then(result => {
            console.log(result, 'result')
            this.getDetail()
          })
        }
      })
    },
    */
    getDetail () {
      let id = this.$route.query.id
      if (!id) {
        return
      }
      // id = 1347;// 生态超市
      bannerService
        .getCustomPage({
          customPageId: id,
          streetId: this.location.townshipId,
          channelId: this.$route.query.channelId || '',
        })
        .then(res => {
          const data = res.data
          console.log(res.data)
          if (data.success) {
            this.title = data.data.name
            localData.sharedesp = data.data.description || '海尔智家商城'
            this.modualList = data.data.modualList
            this.skuMap = data.data.skuMap
            this.couponMap = data.data.couponMap
            this.modualList.map(item => {
              if (item.modualType === 'SWITCH' && localData.shareimg === DEFAULTIMG) {
                localData.shareimg = item.imageList[0].imageUrl // 分享图片取轮播图第一张
              }
              if (item.modualType === 'NAVIGATIONBAR' && item.itemType === 'NAVIGATIONBAR') {
                if (item.navigationList) {
                  this.activeNavId = item.navigationList[0].modualId
                }
                setTimeout(() => {
                  localData.navBarHeight = this.$refs[`anchor${item.id}`][0].clientHeight
                  localData.navBarOffsetTop = this.$refs[`anchor${item.id}`][0].offsetTop - this.$refs.nvaHeader.clientHeight
                }, 500)
                this.$nextTick(() => {
                  // 节流监听滚动事件
                  this.$refs.contentContainer.addEventListener('scroll', this.handleScroll, false)
                })
              }
            });
            this.isSkeletonLoading = false;
            setTimeout(() => {
              this.$refs.contentContainer.scrollTo(0, localData.scrollTop || 0);
            }, 500);
          }

          // 二次分享
          this.$store.dispatch('WeChatSecondShare', {
            title: this.title,
            desc: localData.sharedesp,
            thumImage: localData.shareimg,
          })
        })
    },
    /**
     * banner点击处理逻辑
     */
    bannerClick (item) {
      localData.scrollTop = this.$refs.contentContainer.scrollTop;
      const { linkType, param, hyperType, hyperParam } = item
      if (hyperType) {
        this.$store.dispatch('goPageByType', {
          type: Number(hyperType),
          params: hyperParam,
          env: this.isAliENV ? this.isAliMP ? 'alimp' : 'alienv' : undefined
        })
      } else {
        let relationId = '';
        if (param.indexOf('relationId=') >= 0) {
          relationId = param.split('relationId=')[1]
        }
        this.$store.dispatch('bannerClick', { hyperLinkType: linkType, hyperLink: param, relationId, isWeChatMp: this.isMiniProgram, isByteDanceMP: this.isByteDanceMP, accessToken: this.accessToken });
      }
    },
    /**
     * 跳转商品
     */
    toProdDetail (prod, id) {
      const { productId } = prod
      if (this.isMiniProgram) {
        wx.miniProgram.navigateTo({
          url: `/pages/product/detail/index?productId=${productId}`,
        })
      } else if (this.isByteDanceMP) {
        tt.miniProgram.navigateTo({
          url: `/pages/product/detail/index?productId=${productId}`,
        })
      } else if (this.isAliENV) {
        if (this.isAliMP) {
          my.navigateTo({
            url: `/pages/product-detail/index?productId=${productId}`,
          })
        } else {
          window.location.href = `https://ds.alipay.com/?scheme=${encodeURIComponent(`alipays://platformapi/startapp?appId=2021002120664108&page=pages/product-detail/index&query=${encodeURIComponent(`productId=${productId}`)}`)}`
        }
      } else if (this.isFromRN) {
        window.postMessage(
          JSON.stringify({
            type: 'goProduct',
            data: {
              id: productId,
            },
          })
        )
      } else {
        localData.scrollTop = this.$refs.contentContainer.scrollTop;
        this.$router.push(`/goodsDetail?productId=${productId}`)
      }
    },
    /**
     * 切换navigationBar
     */
    changeNavigationbar (modualId, b) {
      this.activeNavId = modualId
      setTimeout(() => {
        this.$refs.contentContainer.scrollTo(0, this.$refs[`anchor${modualId}`][0].offsetTop - localData.navBarHeight - this.headerHeight)
      }, 100)
    },
    goBack () {
      // this.$router.back();
      // this.$store.dispatch('doHistoryBack');
      if (this.isFromRN) {
        window.postMessage(
          JSON.stringify({
            type: 'goBack',
          })
        )
      } else {
        this.$store.dispatch('doHistoryBack')
        // this.modualList = [];
        // this.couponMap = [];
        // this.skuMap = [];
        // this.title = '';
      }
    },
    toShare () {
      this.$util.gioTrackWithVariable('mall_activity_click_share', {
        activity_id: this.$route.query.id + '',
        activity_type: '动态活动'
      })
      const title = this.title; // 分享标题
      const content = localData.sharedesp; // 分享内容
      const pic = localData.shareimg; // 分享图片，写绝对路径
      const id = this.$route.query.id;
      let url = this.mId
        ? `${location.origin}${location.pathname}?id=${id}&shareId=${this.mId}`
        : `${location.origin}${location.pathname}?id=${id}`

      if (this.isFromRN) {
        window.postMessage('custPageShare|' + title + '|' + content + '|' + pic + '|' + url)
      } else if (this.$util.isHaierApp() || this.$util.isIhaierApp()) {
        this.$util
          .inAppShare({
            title,
            desc: content,
            url, // 不传默认当前url
            thumImage: pic,
          })
          .then(res => console.log(res))
      }
    },
  },
  filters: {
    /**
     * 截取数字整数部分  200.12 -> 200
     */
    splitInt (value) {
      if (value) {
        return value.toFixed(2).split('.')[0]
      } else {
        return 0
      }
    },
    /**
     * 截取数字小数部分 200.12 -> .12
     */
    splitFloat (value) {
      if (value) {
        return `.${value.toFixed(2).split('.')[1]}`
      } else {
        return '.00'
      }
    },
  },
}
